import { Directive, Input, Output, EventEmitter, OnInit, ElementRef } from '@angular/core';
import { NSheetService } from './n-sheet.service';
import { HttpClient } from '@angular/common/http';


@Directive({
  selector: '[n-sheet]'
})
export class NSheetDirective implements OnInit {
  @Input() sheetOptions;
  @Input() htmlContent;
  @Output() onsuccess: EventEmitter<any> = new EventEmitter();
  @Output() onerror: EventEmitter<any> = new EventEmitter();

  constructor(private sheetService: NSheetService, private http: HttpClient, private el: ElementRef) { }

  ngOnInit() {
    this.getSheet()
    .then(success => this.onsuccess.emit(success))
    .catch(error => this.onerror.emit(error));
   }

  getSheet(): Promise<any> {
    return new Promise((resolve, reject) => {
      this.sheetService.getSheet(this.sheetOptions,this.htmlContent).then(res => {
        if(res.hasOwnProperty('excelAsHtml') && res['excelAsHtml'].length > 0 && !Array.isArray(res['excelAsHtml'])) {
          if(!(typeof this.sheetOptions === 'object' && this.sheetOptions.hasOwnProperty('doAppend') && this.sheetOptions.doAppend === true)) {
            this.el.nativeElement.innerHTML = "";
          }
          const fragment = document.createRange().createContextualFragment(res['excelAsHtml']);
          this.el.nativeElement.appendChild(fragment);
          return resolve(res);
        } else {
          return reject(res);
        }
      }).catch(err => reject(err));
    });
  }

}